import React, { useState, useEffect } from "react";
import Nav from "../../components/Navbar/Nav";
import axios from "axios";
import { useParams } from "react-router-dom";
import { toast } from "react-hot-toast";
import { useNavigate } from "react-router-dom";
import { useCart } from "../../Context/cart";
import { FcDown } from "react-icons/fc";
import { FaRupeeSign } from "react-icons/fa";

const Productdetail = () => {
  const Navigate = useNavigate();
  const [product, SetProduct] = useState({});
  const [relatedproduct, SetRelatedProduct] = useState([]);
  const [cart, SetCart] = useCart();

  const params = useParams();
  const GetSimilarProducts = async (pid, cid) => {
    try {
      const { data } = await axios.get(
        `/api/admin/product/related-product/${pid}/${cid}`
      );
      SetRelatedProduct(data?.products);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    const getProduct = async () => {
      try {
        const { data } = await axios.get(
          `/api/admin/product/single-product/${params.slug}`
        );
        SetProduct(data?.product);
        GetSimilarProducts(data?.product?._id, data?.product?.category?._id);
        toast.success(data.message);
      } catch (error) {
        console.log(error);
        toast.error("something went wrog");
      }
    };
    if (params?.slug) getProduct();
  }, [params.slug]);
  return (
    <div>
      <Nav />

      <div className="row">
        <div className="col-md-6 p-5 pt-2">
          {" "}
          <img
            src={`/api/admin/product/product-photo/${product._id}`}
            className="card-img-top     rounded-5"
            height={400}
            alt={product.productName}
          />
        </div>
        <div className="col-md-6 p-5 ">
          {/* <h3 className="text-center text-capitalize font-monospace  "> product detail </h3> */}{" "}
          <h1
            className="card-title text-capitalize  fw-bold"
            style={{ fontSize: "4rem" }}
          >
            {product.productName}{" "}
          </h1>
          <hr />
          <h2 className="fw-bold py-2">
            <span className="text-uppercase">category :</span>{" "}
            {product?.category?.name}
          </h2>
          <h5 className="card-text text-capitalize py-2 ">
            {" "}
            <span className="text-uppercase h3 fw-bold ">about :</span>{" "}
            {product.description}
          </h5>
          <h3 className="card-text fw-bold  mt-4">
            <span className="text-uppercase">price :</span>{" "}
            <FaRupeeSign className="h4" />
            {product.price}.00/-
          </h3>
          <h4 className="card-text fw-bold  mt-4 mb-5">
            {" "}
            Products Left in the store: {product.quantity}
          </h4>
          <hr />
          <div className="d-flex flex-row gap-3 ">
        

         

         
         
          <button
            onClick={() => { 
              SetCart([...cart, product]);
              localStorage.setItem("cart", JSON.stringify([...cart, product]));
              Navigate('/cart')

            }}
            className="btn btn-primary text-uppercase px-5  justify-content-center  font-monospace "
          >
           BUY NOW
          </button>
          <button
          
          onClick={() => {
            SetCart([...cart, product]);
            localStorage.setItem("cart", JSON.stringify([...cart, product]));
            toast.success(` ${product.productName}  added to the cart`);
          }}
          className="btn btn-secondary text-uppercase justify-content-center px-5  py-4  font-monospace  "
        >
          Add To Cart
        </button>
        
          </div>
        </div>
      </div>
      <div className="row px-5 ">
        {/* <span>hii</span> */}
        {/* {JSON.stringify(relatedproduct,null,4)} */}
        <h4 className=" fw-bold">
          similar products{" "}
          <span className="h3">
            <FcDown />
          </span>
        </h4>
        {relatedproduct.length < 1 && (
          <h2 className="text-center">
            {" "}
            <span className="h1 fw-bold text-uppercase text-muted px-2">
              opps!
            </span>
            No Similar product found
          </h2>
        )}
        <div className="d-flex flex-wrap ">
          {relatedproduct?.map((product) => (
            <>
              <div
                className="card m-3 "
                style={{ height: "17rem", width: "18rem" }}
              >
                <img
                  src={`/api/admin/product/product-photo/${product._id}`}
                  className="card-img-top cardimg    rounded-2"
                  alt={product.productName}
                />
                <div className="card-body">
                  <h4 className="card-title text-capitalize  text-center fw-bold">
                    {product.productName}
                  </h4>
                  <hr />

                  <p className="card-text h5">
                    {" "}
                    {product.description.substring(0, 30)}
                  </p>

                  <h5 className="card-text fw-bold">
                    {" "}
                    <FaRupeeSign /> {product.price}.00/-
                  </h5>

                  <div className="d-flex p-2 flex-row gap-2  ">
                    <button
                      onClick={() => Navigate(`/productdetail/${product.slug}`)}
                      className="btn btn-primary text-uppercase "
                    >
                      More Details
                    </button>

                    <button
                      onClick={() => {
                        SetCart([...cart, product]);
                        localStorage.setItem(
                          "cart",
                          JSON.stringify([...cart, product])
                        );
                        toast.success(
                          ` ${product.productName}  added to the cart`
                        );
                      }}
                      className="btn btn-secondary text-uppercase   "
                    >
                      Add To Cart
                    </button>
                  </div>
                </div>
              </div>
            </>
          ))}
        </div>
        {/* <div>{relatedproduct.productName}</div> */}
      </div>
    </div>
  );
};

export default Productdetail;
